import { useParams } from "react-router-dom";

function HolyQuran() {
  return (
    <>
      <div>hq</div>
    </>
  );
}

function Tsp() {
  return (
    <>
      <div>tsp</div>
    </>
  );
}

export default function ProductScreen() {
  const { product_name } = useParams();

  // eslint-disable-next-line default-case
  switch (product_name) {
    case "holy_quran":
      return <HolyQuran />;
    case "tsp":
      return <Tsp />;
    default:
      return <></>;
  }
}
