import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter, Routes, Route, HashRouter } from "react-router-dom";
import AOS from "aos";

import "./styles/index.css";
import "./assets/vendor/aos/dist/aos.css"


import Home from './screens/home';
import NotFound from './screens/notfound';
import HolyQuranPrivacyPolicy from './screens/holyquranprivacy';




import reportWebVitals from "./reportWebVitals";
import ProductScreen from "./screens/product_screens";




// initialize aos for AOS effect

AOS.init();

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
<HashRouter>
  <Routes>
  <Route path="/*" element={<NotFound/>}/>
  <Route path="/" element={<Home/>}/>
  <Route exact path="/products/:product_name" element={<ProductScreen />}/>
  <Route path="/holy_quran/privacy_policy.html" element={<HolyQuranPrivacyPolicy/>}/>
    
  </Routes>
</HashRouter>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
  reportWebVitals();